.card {
  background: #0000;
  width: 200px;
  height: 150px;
  margin: 15px 45px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  border: 3px solid #0000;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#F2D330;
}
.card > .img-container > img {
  width: 60%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}


